.footer { width: 100%; padding-top: 100px; position: relative; }

@media only screen and (max-width: 992px) { .footer { padding-top: 0; } }

.footer #map { height: 400px; }

.footer__contacts { position: absolute; background: #ffffff; width: 370px; height: 370px; right: 478px; top: 180px; padding: 50px; box-shadow: 7.5px 12.99px 30px 0px rgba(51, 51, 51, 0.1); }

@media only screen and (max-width: 1200px) { .footer__contacts { right: 100px; } }

@media only screen and (max-width: 992px) { .footer__contacts { top: 110px; } }

@media only screen and (max-width: 610px) { .footer__contacts { position: relative; top: 0; left: 0; right: 0; bottom: 0; width: 100%; padding-top: 40px; padding-bottom: 40px; height: 100%; } }

.footer__contacts h6 { font-size: 36px; font-family: "Gilroy-Light", sans-serif; font-weight: 100; line-height: 24px; margin-bottom: 56px; }

@media only screen and (max-width: 992px) { .footer__contacts h6 { text-align: center; } }

@media only screen and (max-width: 425px) { .footer__contacts h6 { font-size: 24px; margin-bottom: 20px; } }

.footer__contacts p { position: relative; }

.footer__contacts p:before { content: ''; position: absolute; width: 12px; height: 12px; top: 5px; left: 0; }

.footer__contacts p:nth-of-type(1) { font-size: 18px; font-family: "Gilroy-SemiBold", sans-serif; font-weight: 700; padding-left: 26px; margin-bottom: 19px; }

.footer__contacts p:nth-of-type(1) a { color: #2c3544; }

.footer__contacts p:nth-of-type(1):before { background: url("/img/call-answer.svg") left top no-repeat; background-size: cover; }

.footer__contacts p:nth-of-type(2) { font-family: "Gilroy-Medium", sans-serif; font-size: 16px; font-weight: 500; padding-left: 21px; padding-right: 30px; line-height: 24px; margin-bottom: 24px; }

.footer__contacts p:nth-of-type(2) a { color: #7b818a; }

.footer__contacts p:nth-of-type(2):before { background: url("/img/placeholder-filled-point.svg") left top no-repeat; background-size: cover; }

.footer__contacts p:nth-of-type(3) { color: blue; font-family: "Gilroy-Medium", sans-serif; font-size: 16px; font-weight: 500; padding-left: 24px; padding-right: 30px; line-height: 24px; }

.footer__contacts p:nth-of-type(3) a { color: #7b818a; }

.footer__contacts p:nth-of-type(3):before { background: url("/img/blue-envelope.svg") left top no-repeat; background-size: cover; }

.footer__nav { padding-top: 66px; min-height: 240px; background: #2c3544; padding-left: 20px; }

@media only screen and (max-width: 610px) { .footer__nav { padding-top: 20px; padding-left: 0; } }

.footer__nav_wrapper { width: 100%; display: flex; align-items: center; justify-content: space-between; }

@media only screen and (max-width: 1200px) { .footer__nav_wrapper { justify-content: space-between; } }

@media only screen and (max-width: 610px) { .footer__nav_wrapper { flex-direction: column; } }

.footer .work-schedule { font-size: 10px; font-family: "Gilroy-Medium", sans-serif; font-weight: 500; color: #576b9c; line-height: 20px; padding-top: 40px; margin-left: 70px; }

.footer .work-schedule p { color: #a0aed0; font-size: 12px; }

@media only screen and (max-width: 1200px) { .footer .work-schedule { display: none; } }

.footer .button_footer { margin-right: 20px; }

@media only screen and (max-width: 998px) { .footer .button_footer { margin: 5px 20px 10px 0; } }

@media only screen and (max-width: 767px) { .footer .button_footer { display: none; } }

.modal-form { padding-top: 10px; width: 400px; padding: 10px; background: #fff; margin: 0 auto; border: 5px solid grey; }

@media only screen and (max-width: 420px) { .modal-form { width: 300px; } }

.modal-form a { color: #2c3544; }

.modal-form a:hover { color: red; }

.modal-form h5 { text-align: center; font-size: 28px; line-height: 20px; color: #2c3544; padding-bottom: 30px; }

.modal-form h5 strong { font-size: 16px; color: #7b818a; }

.modal-form .main-form__form_main { margin: 20px 0; }

.modal-form .main-form__form_flex { padding: 0; }

.modal-form .main-form__form_wrapper { margin-bottom: 0; }

.modal-form .main-form__form_checkbox strong::after { left: 5px; top: 2px; width: 5px; height: 9px; border: solid #03a6f1; border-width: 0 2px 2px 0; transform: rotate(45deg); }

.modal-form .main-form__form_droplist { margin-top: 20px; margin-bottom: 35px; max-height: 270px; font-family: "Gilroy-Regular", sans-serif; font-weight: 100; font-size: 16px; }

@media only screen and (max-width: 420px) { .modal-form .main-form__form_droplist { font-size: 14px; } }

.modal-form .close { display: block; text-align: right; font-size: 24px; padding-right: 20px; }

.modal-form .button { margin-top: 10px; margin-bottom: 20px; }

.modal_checkbox { display: block; position: relative; cursor: pointer; user-select: none; font-size: 12px; color: #7b818a; line-height: 16px; padding-top: 10px; text-align: center; width: 280px; margin: 0 auto; }

.close-servises { font-size: 30px; color: red; position: absolute; top: 10px; right: 20px; z-index: 100; }
