.slider-screen {
	min-height: 720px;
	width: 100%;
	background: #00247c;
	@include r(767) {
		min-height: auto;
		padding-top: 40px;
		padding-bottom: 40px; }

	&__info {
		padding-top: 113px;
		padding-left: 15px;
		padding-right: 15px;
		color: #ffffff;
		margin-bottom: 96px;
		@include r(992) {
			padding-top: 30px; }
		@include r(767) {
			padding-top: 0; } } }

.my-slider {
	&__section {
		height: 720px;
		li {
			height: 310px;
			width: 510px;
			margin-top: 23px;
			margin-bottom: 23px;
			img {
				margin-left: 70px;
				width: 430px;
				height: 310px;
				transition: all .6s;
				opacity: .2;
				border-radius: 8px;
				@include r(1200) {
					width: 370px;
					margin-left: 30px; }
				@include r(992) {
					width: 320px;
					margin-left: 15px; } } }
		.slick-current {
			li {
				img {
					transition: all .6s;
					margin-left: 32px;
					width: 510px;
					height: 310px;
					opacity: 1;
					@include r(1200) {
						width: 430px;
						margin-left: 0; }
					@include r(992) {
						width: 340px;
						margin-left: 0; } } } }
		@include r(767) {
			display: none; } }
	&__block {
		@include r(425) {
			padding-left: 20px;
			padding-right: 20px; }
		li {
			color: #ffffff;
			display: block;
			width: 535px;
			padding-left: 15px;
			h5 {
				font-size: 20px;
				font-family: $medium-font;
				font-weight: 500;
				line-height: 30px;
				max-width: 350px;
				margin-bottom: 30px; }
			p {
				font-size: 18px;
				line-height: 30px;
				color: #a0aed0; }
			strong {
				color: #ffffff;
				font-size: 16px;
				margin-top: 37px;
				margin-left: 22px;
				display: block;
				font-weight: 400;
				position: relative;
				img {
					width: 16px;
					height: 16px;
					position: absolute;
					left: -22px; } } }


		.slick-prev, .slick-next {
			position: absolute;
			width: 36px;
			height: 36px;
			top: 365px;
			border-width: 1px;
			border-color: rgba(3, 166, 241, .2);
			border-style: solid;
			border-radius: 50%;
			@include r(992) {
				top: -40px; } }
		.slick-prev {
			left: 17px;
			&::before {
				opacity: 1;
				content: url("../../img/prev.svg"); }
			@include r(991) {
				left: 40%;
				transform: translateX(-60%); }
			@include r(767) {
				left: 45%;
				transform: translateX(-55%); } }
		.slick-next {
			left: 62px;
			&::before {
				opacity: 1;
				content: url("../../img/next.svg"); }
			@include r(991) {
				left: 60%;
				transform: translateX(-40%); }
			@include r(767) {
				left: 55%;
				transform: translateX(-45%); } } } }
